import React from 'kurge'
import useI18N from '../i18n'
import context from '../context'

export default function Banner() {
  const I18N = useI18N()
  function changeLang() {
    context.lang = context.lang === 'CN' ? 'EN' : 'CN'
  }
  return (
    <header className="Banner" style={{ height: window.innerHeight + 'px' }}>
      <h1 className="title">Kurge</h1>
      <p className="desc">{I18N[0]}</p>
      <div className="opr">
        <a onClick={() => open("https://github.com/Siubaak/kurge/wiki/Get-Started")}>{I18N[1]}</a>
        <a onClick={() => open("https://github.com/Siubaak/kurge/wiki/Documentation")}>{I18N[2]}</a>
      </div>
      <div className="link">
        <a onClick={changeLang}>{context.lang === 'CN' ? 'EN' : 'CN'}</a>
        <a onClick={() => open("https://github.com/Siubaak/kurge")}>GITHUB</a>
        <a onClick={() => open("https://www.npmjs.com/package/kurge")}>NPM</a>
      </div>
    </header>
  )
}